import logo from './logo.svg';
import 'bulma/css/bulma.min.css';
import './App.css';
import sticks from './crossedSticks.png';
import { useState, useEffect } from 'react';
import ReactImageAppear from 'react-image-appear';
import SubmitButton from './SubmitButton';
import RenderTeams from './RenderTeams';
import FormComponent from './FormComponent';
import Sidebar from './Sidebar';
import TeamFormComponent from './TeamFormComponent';
import {  useRef } from 'react'
import backgroundHockey from './FullImage.jpg';



//Top level component. Handles the switching of form types, including the type of button (submit or reset).

function App() {

  const [submitType, updateType] = useState('Players');
  const [color1, updateColor1] = useState('typeButton2');
  const [color2, updateColor2] = useState('typeButton');
  const [formCount, addCount] = useState(1);
  const [resetCount, updateReset] = useState(0);
  const messageRef = useRef();


  useEffect(() => {
    if (messageRef.current) {
      messageRef.current.scrollIntoView(
        {
          behavior: 'smooth',
          block: 'end',
          inline: 'nearest'
        })
    }
  })


  if (submitType === "Players" && color1 !== 'typeButton2') {
    updateColor1('typeButton2'); updateColor2('typeButton');
  } else if (submitType === "Teams" && color2 !== 'typeButton2') {
    updateColor2('typeButton2'); updateColor1('typeButton');
  }


  return (
    <div ref = {messageRef} id="mainDiv">


      <div className="section headerSection">
        <div className="container">
          <h1 id="title">NHL Comparison Tool<img className="headPic" src={sticks} alt="headPic" /></h1>
          <br />
          <br />
          <div className="headerOptions">
          <h2>Compare: <button className={color1} onClick={() => { updateType('Players'); addCount(1) }}><h2>Players</h2></button> <button className={color2} onClick={() => { updateType('Teams'); addCount(1); }}><h2>Teams</h2></button></h2>
          <SubmitButton formType={submitType} updateType={updateType} resetCount = {resetCount} updateReset= {updateReset} addCount={addCount}/>
          </div>
        </div>
      </div>
      <Forms type={submitType} formCount={formCount} addCount={addCount} resetCount = {resetCount} />
    </div >
  );
}


//Handles the information stored 
function Forms(props) {

  console.log(props.resetCount);

  const [formType, updateType] = useState(props.type);
  const [formObjs, updateObjs] = useState([{ prop1: '1', prop2: '2' }, { prop3: '3', prop4: '4' }])


  if (formType !== props.type) {

    updateType(props.type);
  }



  /////////////////////////////////////////////////////////////////////////////// Return code


  if (formType === 'Players') {


    let rows = [];

    for (let i = 0; i <= props.formCount; i++) {

      if (i > 3) {
        break;
      }


      rows.push(


        <FormComponent formType={formType} formCount={props.formCount} formid={i + 1} addCount={props.addCount} resetCount = {props.resetCount}/>

      );
    }



    return (
      <section>
        <div className='container'>


          <div className="columns is-variable">

            {rows}

          </div>

        </div>
      </section>

    );

  } else if (formType === "PlayerCards") {

    let rows = [];

    for (let i = 0; i < props.formCount; i++) {


      rows.push(


        <FormComponent formType={formType} formCount={props.formCount} formid={i + 1} addCount={props.addCount} />

      );
    }

    return (
      <section>
        <div className='container'>


          <div className="columns is-variable">

            {rows}

          </div>

        </div>
      </section>

    );



  } else if (formType === 'Teams') {


    let rows = [];

    for (let i = 0; i <= props.formCount; i++) {

      if (i > 3) {
        break;
      }

      rows.push(



        <TeamFormComponent formType={formType} formCount={props.formCount} formid={i + 1} addCount={props.addCount} />



      );


    }


    return (

      <section>
        <div className="container">

          <div className="columns is-variable">


            {rows}


          </div>


        </div>
      </section>

    );



  } else {

    let rows = [];

    for (let i = 0; i < props.formCount; i++) {


      rows.push(



        <TeamFormComponent formType={formType} formCount={props.formCount} formid={i + 1} addCount={props.addCount} />



      );


    }


    return (

      <section>
        <div className="container">

          <div className="columns is-variable">


            {rows}


          </div>


        </div>
      </section>

    );
    

  }









}

export default App;
